export default {
  data: function () {
    return {
      hotBusiness: []
    }
  },
  methods: {
    refresh: function () {
      this.$http.get(this.url('business/getHotBusiness')).then(this.rspHandler((data)=> {
        this.hotBusiness = data
      }))
    }
  },
  created: function () {
    window.vm.$refs.header.showBanners = true;
    this.refresh()
  }
}



