export default {
  data: function () {
    return {
      business: []
    }
  },
  methods: {
    refresh: function () {
      this.getSelections('business').then((data)=>{
        this.business = data
      })
    },
  },
  created: function () {
    window.vm.$refs.header.showBanners = true;
    this.refresh()
  }
}



