export default {
  data: function () {
    return {
      formPop: false,
      modalLoading:false,
      form:{
        title:'',
        content:'',
        type:[]
      },
      rule:{

      },
      selections: {
        city: [{
          value: 'beijing',
          label: '北京',
          children: [
            {
              value: 'gugong',
              label: '故宫'
            },
            {
              value: 'tiantan',
              label: '天坛'
            },
            {
              value: 'wangfujing',
              label: '王府井'
            }
          ]
        }, {
          value: 'jiangsu',
          label: '江苏',
          children: [
            {
              value: 'nanjing',
              label: '南京',
              children: [
                {
                  value: 'fuzimiao',
                  label: '夫子庙',
                }
              ]
            },
            {
              value: 'suzhou',
              label: '苏州',
              children: [
                {
                  value: 'zhuozhengyuan',
                  label: '拙政园',
                },
                {
                  value: 'shizilin',
                  label: '狮子林',
                }
              ]
            }
          ],
        }]
      }
    }
  },
  methods: {
    showPop:function(){
      this.formPop = true;
    },
    addProblem:function(){
      this.modalLoading = true
      this.$refs.form.validate((valid) => {
        this.modalLoading = false
        if (valid) {
          this.$Message.success('提交成功!');
        } else {
          this.$Message.error('表单验证失败!');
        }
      });
    }
  },
  created: function () {
    window.vm.$refs.header.showBanners = false;
  }
}



